import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Global')
export class Global extends Component {

	@property({type: Component}) 
    public Instance: Component|null = null;

	@property({type: Number}) 
    winCount = 0;

    @property({type: Number}) 
    totalPlayed = 0;

    @property({type: Number}) 
    entires = 0;

    @property({type: Number}) 
    entireLevel = 0;

    @property({type: Number}) 
    ways = 1024;

    @property({type: Number}) 
    coinValue = 0;

    @property({type: Number}) 
    totalWinPrice = 0;

    @property({type: Number}) 
    soundStatus = 0;

    @property({ type: [cc.String] })
	reelPattern: string[] = [];

	onLoad(){
        cc.gb = {};
        cc.bg.user = {
        	winCount: 0,
        	totalPlayed: 0,
        	entires: 0,
        	entireLevel: 0,
        	ways: 1024,
        	coinValue: 0,
        	totalWinPrice: 0,
        };

        cc.gb.gameEnv = {
        	soundStatus: true,
        	reelPattern: [],
        };


        console.log('onload = ', cc.user.ways);
    }

}

